*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

svg {
  width: 100%;
}

button {
  cursor: pointer;
}

.nav {
  height: 20vh;
  background: #141415;
  position: sticky;
  top: 0;
  z-index: 9999;
}

.nav nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 30px 50px;
}

.nav nav .logo img {
  width: 150px;
}

.nav nav .list {
  width: 50vw;
  display: flex;
  justify-content: space-between;
}

.nav nav .list ul {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.nav nav .list ul li {
  list-style-type: none;
  color: #f9f7f4;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav nav .list button {
  display: block;
  padding: 15px 20px;
  border-radius: 10px;
  border: 0;
  background: #ed027e;
  color: #f9f7f4;
  letter-spacing: 1.5px;
}
